export default `<div id="magicdomid2_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">欢迎您使用抓饭直播软件及相关服务！</span></div><div id="magicdomid3_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">为了更好地为您提供服务，请您仔细阅读《抓饭直播服务协议及隐私条款》（以下简称&ldquo;本协议&rdquo;）。在您开始使用抓饭直播软件及相关服务之前，请您务必认真阅读并充分理解本协议，特别是涉及免除或者限制责任的条款、权利许可和信息使用的条款、同意开通和使用特殊单项服务的条款、法律适用和争议解决条款等。如您未满18周岁，请您在法定监护人陪同下仔细阅读并充分理解本协议，并征得法定监护人的同意后下载本软件。</span></div><div id="magicdomid4_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">除非您完全接受本协议的全部内容，否则您无权下载、安装、注册、登录、使用抓饭直播软件，或者通过任何方式使用抓饭直播服务，或者获得抓饭直播软件提供的任何服务（以下统称&ldquo;使用&rdquo;）。若您使用&ldquo;抓饭直播&rdquo;软件及相关服务，则视为您已充分理解本协议并承诺作为本协议的一方当事人接受本协议的约束。</span></div><div id="magicdomid5_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">1. 特别提示</span></div><div id="magicdomid6_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">1.1、本协议是用户关于注册、登录抓饭直播视频直播平台（下称&ldquo;</span><span class="author-p-1481532 bold:true">视频直播平台</span><span class="author-p-1481532">&rdquo;或&ldquo;</span><span class="author-p-1481532 bold:true">抓饭直播平台</span><span class="author-p-1481532">&rdquo;或&ldquo;</span><span class="author-p-1481532 bold:true">平台</span><span class="author-p-1481532">&rdquo;）以及使用平台服务所订立的协议，本协议约定了平台与用户之间关于抓饭直播服务方面的权利义务。 &ldquo;用户&rdquo;是指注册、登录、使用、浏览、获取本协议项下服务的个人或组织。</span></div><div id="magicdomid7_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">1.2、用户应认真阅读（未成年人应当在监护人陪同下阅读）、充分理解本协议中各条款，特别是免除或者限制平台责任的免责条款及对用户的权利限制条款，并选择接受或不接受本协议。除非用户接受本协议所有条款，否则用户无权注册、登录或使用抓饭直播为用户提供的服务。</span><span class="author-p-1481532 bold:true">用户的注册、登录、使用等行为将视为对本协议的接受，并同意接受本协议各项条款的约束。</span></div><div id="magicdomid8_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">1.3、通过访问和/或使用抓饭直播，用户表示同意接受本协议的所有条件和条款。抓饭直播的网站（</span><span class="author-p-1481532 attrlink url"><a class="attrlink" href="https://www.justfun.fun">www.justfun.fun</a></span><span class="author-p-1481532">）、软件及APP面向最终用户提供实时在线演绎、互动直播等服务。如果用户不愿接受本协议的全部条件和条款，请用户不要访问或使用抓饭直播。如果用户愿意接受本协议，则意味着用户了解及完全同意本协议的各项内容，以及抓饭直播对本协议及本协议附件随时所做的任何修改。</span></div><div id="magicdomid9_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">1.4、本协议下的条款可由抓饭直播随时变更，用户须定期审阅本协议。协议条款一旦发生变动，抓饭直播将会在网站、软件、APP相关的页面上发布。</span><span class="author-p-1481532 bold:true">变更后的协议在相关的页面上公布即有效代替原来的协议，恕不再另行通知。用户如不同意抓饭直播对本协议的所作的任何变更，应立即停止使用抓饭直播提供的服务。如用户在本协议变更后继续使用抓饭直播的服务，即视作用户已完全同意变更后的协议。</span></div><div id="magicdomid10_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">1.5、请注意：鉴于用户使用网络服务之习惯，用户有权利且有义务自使用抓饭直播服务之日起每月自行主动对本协议之全部内容进行重新阅读，</span><span class="author-p-1481532 bold:true">鉴于上述约定，用户因自身怠于行使该等权利或怠于履行该等义务的，无权以未获知上述内容而对相关条款的法律效力提出异议。</span></div><div id="magicdomid11_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid12_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2. 服务内容</span></div><div id="magicdomid13_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.1、抓饭直播是中国互联网的体育等视频直播平台，抓饭直播本身不直接上传内容。</span></div><div id="magicdomid14_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.2、抓饭直播运用自己的系统通过互联网向用户提供服务，除非另有明确规定，增加或改变目前服务的任何新功能，包括新产品以及新增加的服务，均无条件地适用本协议。</span></div><div id="magicdomid15_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.3、抓饭直播并不保证所提供的网站服务一定会满足用户的使用要求，也不保证服务不会被中断，对服务的及时性、安全性、准确性也不作担保。</span><span class="author-p-1481532 bold:true">抓饭直播保留随时变更、中断或终止部分或全部网络服务包括收费网络服务的权利。</span></div><div id="magicdomid16_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.4、</span><span class="author-p-1481532 bold:true">对于因服务器的死机、网络的故障、数据库故障、软件升级、服务器维修、调整、升级等问题，或其他不可抗拒的事由，包括但不限于政府行为、自然灾害、黑客攻击等造成的服务中断和对用户个人数据及资料造成的损失，抓饭直播不承担由此对用户造成的任何损失或退还任何已缴纳的费用。</span></div><div id="magicdomid17_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.5、用户理解：抓饭直播仅提供相关的网络服务，除此之外与相关网络服务有关的设备(如电脑、网络服务接入及其他与接入互联网有关的装置)及所需的费用(如为接入互联网而支付的电话费及上网费)均应由用户自行负担。</span></div><div id="magicdomid18_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.6、用户应使用正版软件接受网络服务。</span></div><div id="magicdomid19_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.7、关于用户付费的特别提示：</span></div><div id="magicdomid20_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">(A)用户选择接受付费服务，则必须按照抓饭直播的规则进行支付，方可享受对应的服务。如果用户拒绝支付，则抓饭直播有权拒绝向用户提供该等服务。</span></div><div id="magicdomid21_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">(B) 在抓饭直播系统中，用户通过任何支付手段（包括但不限于网银充值、实物卡充值、红包充值等）为其账户充值所直接获得的均为&ldquo;米币&rdquo;（或抓饭直播更新发布的其他虚拟资产）；</span></div><div id="magicdomid22_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">(C) 米币可以用于支付抓饭直播的相关互联网应用及内容的费用。</span></div><div id="magicdomid23_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">(D) 用户理解并知晓，抓饭直播不提供用户间交易米币的平台化服务。</span><span class="author-p-1481532 bold:true">用户间交易米币系构成对本协议的违反，抓饭直播有权在未经通知的情况下，采取相应适当措施，以确保抓饭直播不提供用户间交易米币币的平台化服务。</span></div><div id="magicdomid24_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.8、抓饭直播在服务过程中，可能基于自身服务需求或国家法律法规的要求，要求用户提交个人信息。在此情况下，用户必须按照抓饭直播的要求提交真实个人信息或进行实名认证，才能享受服务。如果用户拒绝提供个人信息，则抓饭直播有权拒绝用户对该服务的继续访问。</span></div><div id="magicdomid25_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">2.9、抓饭平台对直播服务提供者建立信用等级管理体系，提供与信用等级挂钩的管理和服务。</span></div><div id="magicdomid26_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid27_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3. 使用规则</span></div><div id="magicdomid28_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.1 、用户在接受本协议之后，有权选择未被其他用户使用过的字母符号组合作为用户的账户，并自行设置符合安全要求的密码。用户设置的账户、密码是用户用以接受抓饭直播服务的凭证。</span></div><div id="magicdomid29_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.2、抓饭直播账户仅限于抓饭直播网站上注册用户本人使用，禁止赠与、借用、租用、转让或售卖。</span><span class="author-p-1481532 bold:true">如果抓饭直播发现使用者并非账户初始注册人，有权在未经通知的情况下回收该账户而无需向该账户使用人承担法律责任，由此带来的包括并不限于用户通讯中断、用户资料和道具、虚拟资产等清空等损失由用户自行承担。</span></div><div id="magicdomid30_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.3、用户应当妥善保管自己的抓饭直播账户、密码，用户就其账户及密码项下之一切活动负全部责任，包括用户数据的修改、账户财物的损失以及其他所有的损失由用户自行承担。用户须重视抓饭直播账户密码保护。鉴于网络服务的特殊性，</span><span class="author-p-1481532 bold:true">抓饭直播无义务审核是否用户本人使用该组账户及密码，仅审核账户及密码是否与数据库中保存的一致，只要任何人输入的账户及密码与数据库中保存的一致，即可凭借该组账户及密码登陆并获得网络服务，所以即使用户认为其所有的账户登陆行为并非其本人所为，抓饭直播将不承担因此而产生的任何责任</span><span class="author-p-1481532">。</span></div><div id="magicdomid31_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.4、用户发现其账户或密码被他人非法使用或有使用异常的情况的，应及时通知抓饭直播，并有权通知抓饭直播采取措施暂停该账户的登录和使用。抓饭直播在收到用户要求采取措施暂停其账户登录和使用的通知后，应当要求用户提供并核实与其注册身份信息相一致的个人有效身份信息及其他数据信息以证明该账户所有权，在抓饭直播认为必要时，可以要求用户提供担保。</span></div><div id="magicdomid32_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.5、用户同意接受抓饭直播通过电子邮件或其他方式向用户发送的信息，包括但不限于商品促销及其他相关商业信息。</span></div><div id="magicdomid33_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.6、用户在使用抓饭直播服务过程中，必须遵循以下原则：</span></div><div id="magicdomid34_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.6.1 遵守中国有关的法律和法规；</span></div><div id="magicdomid35_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.6.2 遵守所有与网络服务有关的以及抓饭直播制定的网络协议、规定、规则和程序；</span></div><div id="magicdomid36_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.6.3 发现任何非法使用用户账户或账户出现安全漏洞的情况，应立即通告抓饭直播。</span></div><div id="magicdomid37_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7、禁止用户在接受网络服务过程中进行以下侵害行为，包括但不限于：</span></div><div id="magicdomid38_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.1 违反宪法确定的基本原则的；</span></div><div id="magicdomid39_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.2 危害国家统一、主权和领土完整的；</span></div><div id="magicdomid40_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.3 泄露国家秘密、危害国家安全或者损害国家荣誉和利益的；</span></div><div id="magicdomid41_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.4 煽动民族仇恨、民族歧视，破坏民族团结，或者侵害民族风俗、习惯的；</span></div><div id="magicdomid42_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.5 宣扬邪教、迷信的；</span></div><div id="magicdomid43_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.6 散布谣言，扰乱社会秩序，破坏社会稳定的；</span></div><div id="magicdomid44_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.7 宣扬淫秽、色情、赌博、非法彩票、暴力，或者教唆犯罪的；</span></div><div id="magicdomid45_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.8 侮辱、诽谤他人，侵害他人合法权益的；</span></div><div id="magicdomid46_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.9 违背社会公德的；</span></div><div id="magicdomid47_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.7.10 有法律、行政法规和国家规定禁止的其他内容的。</span></div><div id="magicdomid48_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8 、禁止用户进行以下侵害网络服务安全性的行为，包括但不限于：</span></div><div id="magicdomid49_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.1 以任何形式违反诚实信用原则，直接或间接采取组织、教唆、窃取、占有、使用、捡取、购买、转卖等手段侵犯任何第三方拥有使用权的账户、虚拟资产等权益；</span></div><div id="magicdomid50_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.2 以任何积极或消极的形式协助他人侵犯第三方权益的；</span></div><div id="magicdomid51_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3 用户利用网络服务无关的行为，包括但不限于：</span></div><div id="magicdomid52_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.1 为任何非法目的及与抓饭直播提供服务或产品未有直接关系而使用网络服务系统；包括但不限于买卖号、虚拟货币、虚拟物品等；</span></div><div id="magicdomid53_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.2 未经抓饭直播授权访问或试图访问及抓饭直播提供的相关网络服务相关的任何账户、计算机或网络；</span></div><div id="magicdomid54_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.3 未经抓饭直播授权利用抓饭直播提供的相关网络服务以任何方式收集任何其它用户的信息，包括但不限于用户的个人身份信息和通讯信息；</span></div><div id="magicdomid55_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.4 下载、安装或使用未经抓饭直播授权开发并正式发布的其它任何网络服务或产品；</span></div><div id="magicdomid56_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.5 接收或下载由其他抓饭直播用户传输的用户所知道或应当知道不能以此方式合法传播的任何材料；</span></div><div id="magicdomid57_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.6 利用抓饭直播提供的相关网络服务进行任何可能对互联网的正常运转造成不利影响的行为，包括但不限于以任何方式传输含有计算机病毒、破坏性程序的文件或其他任何可能对他人计算机或互联网的正常运转造成不利影响的软件或程序；</span></div><div id="magicdomid58_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.7 利用抓饭直播提供的相关网络服务传输任何骚扰性的、中伤他人的、辱骂性的、恐吓性的、庸俗淫秽的或其他任何非法的信息资料；</span></div><div id="magicdomid59_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.8 利用抓饭直播提供的相关网络服务进行任何不利于抓饭直播的行为；</span></div><div id="magicdomid60_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.9 利用抓饭直播服务系统进行任何洗钱、预付卡套现、购买和销售非法取得虚拟货币等违法行为；</span></div><div id="magicdomid61_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.8.3.10 就抓饭直播及合作商业伙伴的服务、产品、业务咨询应采取相应机构提供的沟通渠道，在公众场合发布有关抓饭直播及相关服务的负面宣传。</span></div><div id="magicdomid62_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.9、抓饭直播账户所有权属于抓饭直播，抓饭直播有权根据运营需要，发布抓饭直播产品、服务项目、相关活动规则规范。用户使用根据本协议获得的用户账户和密码登录抓饭直播的网站或接受其他抓饭直播提供的服务项目时，应遵守该网站或服务项目的相关公告、通知、服务协议及使用守则，</span><span class="author-p-1481532 bold:true">用户登录上述网站或接受上述服务项目时即视为对相关服务协议及使用守则的接受</span><span class="author-p-1481532">。</span></div><div id="magicdomid63_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.10、如用户违反法律法规、抓饭直播各服务协议或规则的规定，对抓饭直播或任何第三方造成的经济损失或名誉损失，抓饭直播有权根据相关规则进行违规判定，并采取相应限制或处罚措施，包括但不限于：限制或冻结用户对抓饭直播账户的使用，列入黑名单，限制或停止某项单独服务，扣除账户财物，并根据实际情况决定是否恢复使用。</span></div><div id="magicdomid64_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">3.11、</span><span class="author-p-1481532 bold:true">为了充分利用抓饭直播账户资源，若用户存在长期未登录使用抓饭直播账户的情形，抓饭直播有权对该号码进行回收、替换等终止使用操作。</span></div><div id="magicdomid65_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">3.12、抓饭直播依照本协议规则限制、冻结、回收、替换、列入黑名单或终止用户抓饭直播账户的使用，可能会给用户造成一定的损失，该损失由用户自行承担，抓饭直播不承担任何责任</span><span class="author-p-1481532">。</span></div><div id="magicdomid66_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid67_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4. 内容所有权</span></div><div id="magicdomid68_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.1、抓饭直播提供的网络服务内容可能包括：抓饭直播运营的网站、网页运用、软件以及内含的文字、图片、视频、音频等元素，抓饭直播对其服务标志、标识以及上述任何元素享有全球范围内独占的全部知识产权。</span></div><div id="magicdomid69_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2、禁止用户进行以下侵害网络服务知识产权权益的行为，包括但不限于：</span></div><div id="magicdomid70_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2.1 复制、翻拷、传播和在网络上陈列抓饭直播相关服务及产品的程序、使用手册和其它图文音像资料的全部或部分内容；</span></div><div id="magicdomid71_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2.2 公开展示和播放抓饭直播相关服务及产品的全部或部分内容；</span></div><div id="magicdomid72_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2.3 出租抓饭直播相关服务产品于他人；</span></div><div id="magicdomid73_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2.4 对抓饭直播的网络服务或相关产品的程序、图像、动画和音乐进行还原、反编译、反汇编、剪辑、翻译和改编等任何修改行为；</span></div><div id="magicdomid74_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2.5 修改或遮盖抓饭直播相关网络服务或相关产品程序、图像、动画、包装和手册等内容上的产品名称、公司标志、版权信息等内容；</span></div><div id="magicdomid75_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2.6 用户不得对抓饭直播的网络服务或相关产品进行反向工程(Reverse Engineering)、反向编译(Decompile)或反汇编(Disassemble)等任何技术性的与合理使用网络服务无关的行为；</span></div><div id="magicdomid76_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2.7 将抓饭直播相关网络服务作为营业使用；</span></div><div id="magicdomid77_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.2.8 其它违反著作权法、计算机软件保护条例和相关法规的行为。</span></div><div id="magicdomid78_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.3、</span><span class="author-p-1481532 bold:true">如用户违反法律法规、抓饭直播各服务协议（包括但不限于本协议）或规则的规定，抓饭直播有权就任何主体侵权而以抓饭直播自己的名义单独或委托第三方机构提起诉讼或开展其他法律行动，并获得全部赔偿。</span></div><div id="magicdomid79_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">4.4、用户在平台提供直播服务期间产生的任何成果（包括但不限于直播视频、音频，以下统称&ldquo;</span><span class="author-p-1481532 bold:true">主播成果</span><span class="author-p-1481532">&rdquo;）的知识产权由用户享有。</span><span class="author-p-1481532 bold:true">用户同意许可抓饭直播在全世界范围内永久性地、不可撤销地、独家地使用和抓饭直播再许可及（或）转授权任意第三方使用主播成果，包括但不限于复制权、发行权、出租权、展览权、表演权、放映权、广播权、信息网络传播权、摄制权、改编权、翻译权、汇编权以及《著作权法》规定的由著作权人享有的其他著作权相关权利，抓饭直播无须对此额外向用户支付任何费用。</span></div><div id="magicdomid80_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid81_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5. 用户信息保护</span></div><div id="magicdomid82_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.1、未成年人条款：</span></div><div id="magicdomid83_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.1.1如果用户未满18周岁的，需要特别注意未成年人使用条款，并且应在监护人监护、指导下阅读本协议和使用平台项下的服务。</span></div><div id="magicdomid84_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.1.2未满18周岁的用户（尤其是十岁以下用户）若要使用平台项下的服务，必须在取得其父母（或其他监护人）同意后、以其父母（或其他监护人）名义申请注册。</span></div><div id="magicdomid85_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.1.3未成年人用户在使用平台项下的服务时应注意提高安全意识，加强自我保护。</span></div><div id="magicdomid86_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.1.4监护人应对未成年人使用平台项下的服务时多做引导，并且加以判断该服务是否符合于未成年人。</span></div><div id="magicdomid87_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.2 、在不透露单个用户隐私资料的前提下，抓饭直播有权对整个用户数据库进行技术分析并对已进行分析、整理后的用户数据库进行商业上的利用。抓饭直播将采取积极的措施保护用户的注册信息，但是仍然不能保证现有的安全技术措施使用户的技术信息等不受任何形式的损失，</span><span class="author-p-1481532 bold:true">对于非抓饭直播及/或平台可控制的因素导致用户信息泄露的，抓饭直播及/或平台不承担责任。</span></div><div id="magicdomid88_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.3、未经用户许可抓饭直播不得向任何第三方公开或共享用户注册资料中的姓名、个人有效身份证件号码、联系方式、家庭住址等个人身份信息，但下列情况除外：</span></div><div id="magicdomid89_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.3.1 用户或用户监护人授权抓饭直播披露的；</span></div><div id="magicdomid90_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.3.2 有关法令要求抓饭直播披露的；</span></div><div id="magicdomid91_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.3.3 司法机关或行政机关依法要求抓饭直播提供的；</span></div><div id="magicdomid92_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.3.4 抓饭直播为了维护自己合法权益而向用户提起诉讼或者仲裁时；</span></div><div id="magicdomid93_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.3.5 应用户或用户监护人的合法要求而提供用户个人身份信息时；</span></div><div id="magicdomid94_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.3.6 为维护社会公众的利益</span></div><div id="magicdomid95_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.3.7 其他抓饭直播认为合适的且法律并不禁止的披露。</span></div><div id="magicdomid96_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.4、用户在使用抓饭直播平台服务时请勿将自认为隐私的信息发表、上传至抓饭直播平台，也不可将该等信息通过抓饭直播平台的服务器传播给他人，</span><span class="author-p-1481532 bold:true">若因用户该等行为引起隐私泄露，由用户自行承担责任。</span></div><div id="magicdomid97_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">5.5、请用户注意勿在使用抓饭直播平台服务时透露自己的各类财产账户、银行卡、信用卡、第三方支付账户及对应密码等重要资料，</span><span class="author-p-1481532 bold:true">否则由此带来的任何损失由用户自行承担</span><span class="author-p-1481532">。</span></div><div id="magicdomid98_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid99_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">6. 免责声明</span></div><div id="magicdomid100_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">6.1、抓饭直播所有者不保证以下事宜：</span></div><div id="magicdomid101_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">(i) 本服务将符合用户的要求；</span></div><div id="magicdomid102_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">(ii) 本服务将不受干扰、及时提供、安全可靠或不会出错。</span></div><div id="magicdomid103_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">6.2、</span><span class="author-p-1481532 bold:true">用户明确同意其使用抓饭直播服务所存在的风险将完全由其自己承担；因其使用抓饭直播服务而产生的一切后果也由其自己承担，抓饭直播对用户不承担任何责任。</span></div><div id="magicdomid104_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">6.3、用户理解并确认，在使用平台项下的服务的过程中，可能会遇到不可抗力（指不能预见、不能克服并不能避免且对一方或双方造成重大影响的客观事件，包括但不限于自然灾害如洪水、地震、瘟疫流行和风暴等以及社会事件如战争、动乱、政府行为）等风险因素，使本服务发生中断。出现上述情况时，抓饭直播将努力在第一时间与相关单位配合，及时进行修复，由此给用户或第三方造成的损失，抓饭直播及合作单位在法律允许的范围内免责，但会尽最大努力减少用户因此受到的损失。</span></div><div id="magicdomid105_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">6.4、用户理解并确认，用户在使用抓饭直播有违法、不真实、不正当、侵犯第三方合法权益的行为，或用户违反本协议项下的任何条款而给抓饭直播及任何其他第三方造成损失，由用户承担由此造成的损害赔偿责任，抓饭直播不承担任何赔偿责任。</span></div><div id="magicdomid106_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid107_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">7. 拒绝提供担保</span></div><div id="magicdomid108_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">7.1 、</span><span class="author-p-1481532 bold:true">用户自行对网络服务的使用承担风险。抓饭直播对以下事宜不作任何类型的担保，不论是明确的或隐含的</span><span class="author-p-1481532">：</span></div><div id="magicdomid109_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">7.1.1 本协议项下抓饭直播提供的相关网络服务将符合用户的要求；</span></div><div id="magicdomid110_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">7.1.2 本协议项下抓饭直播提供的相关服务将不受不可抗力、计算机病毒、黑客攻击、系统不稳定、用户所在位置、用户关机、电信部门原因及其他任何网络、技术、通信线路等外界或人为因素的影响；</span></div><div id="magicdomid111_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">7.1.3 安装、复制、访问网站、充值、运行软件或以其它方式使用抓饭直播及/或接受抓饭直播提供的相关服务与任何其他软件不存在任何冲突；</span></div><div id="magicdomid112_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">7.1.4 通过抓饭直播网站及其他相关网络上的链接和标签所指向的第三方的商业信誉及其提供服务的质量。</span></div><div id="magicdomid113_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid114_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8. 服务变更、中断或终止</span></div><div id="magicdomid115_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.1、</span><span class="author-p-1481532 bold:true">因抓饭直播战略方向调整或其他原因，抓饭直播有权随时删减、变更某些服务或全部服务，对于这种变更抓饭直播将尽可能的做到事先告知并保护用户在抓饭直播存储的个人信息和事先支付的费用。除此之外，抓饭直播不承担任何责任。</span></div><div id="magicdomid116_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.2、因系统维护或升级的需要，或其他原因，而需暂停部分或全部网络服务，抓饭直播将尽可能事先进行通告。</span></div><div id="magicdomid117_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.3 、用户在接受抓饭直播服务时实施不正当行为的，抓饭直播有权终止对用户提供服务及/或列入黑名单，该不当行为包括但不限于违约、违法、违反公序良俗、侵犯抓饭直播或任何第三方之权益等。</span></div><div id="magicdomid118_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.4、用户在接受抓饭直播服务时实施本协议中有明确约定或属于抓饭直播事先明确告知的应被终止服务的禁止性行为的，抓饭直播有权终止对用户提供服务及列入黑名单。</span></div><div id="magicdomid119_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.5、用户提供虚假注册身份信息，或实施违反本协议的行为，抓饭直播采取中止措施的，应当通知用户并告知中止期间，中止期间应该是合理的，中止期间届满抓饭直播应当及时恢复对用户的服务。</span></div><div id="magicdomid120_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.6、用户利用抓饭直播服务进行洗钱、套现、购买和销售非法取得虚拟货币等违法行为的，抓饭直播有权在不事先通知用户的情况下中止或终止部分或全部网络服务或列入黑名单的权利。</span></div><div id="magicdomid121_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.7、如果用户在注册抓饭直播后连续6个月时间内，没有使用过账户登陆，则抓饭直播有权终止或中止向该账户服务。</span></div><div id="magicdomid122_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.8 、用户接受、使用或在相关更新条款发布后继续使用抓饭直播服务的行为，视为接受并认可相关条款的法律效力及对双方的约束力，且用户认可相关条款（包括但不限于任何更新后的条款）具有溯及至用户注册成为抓饭直播用户之日的法律效力并在该日起对双方有共同的约束力。</span></div><div id="magicdomid123_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">8.9、抓饭直播需要定期或不定期地对提供网络服务的平台进行检测或者更新，如因此类情况而造成网络服务在合理时间内的中断，抓饭直播无需为此承担任何责任。</span></div><div id="magicdomid124_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid125_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">9. 违约赔偿</span></div><div id="magicdomid126_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">9.1、用户同意保障和维护抓饭直播及其他用户的利益，如因用户违反有关法律、法规或本协议项下的任何条款而给抓饭直播或任何其他第三方造成损失，用户同意承担由此造成的损害赔偿责任，该等责任包括但不限于给抓饭直播造成的任何直接或间接损失。</span></div><div id="magicdomid127_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">9.2 、因用户违反本协议约定之任何内容时，包括但不限于抓饭直播通过内部的监测程序发现或经其他用户举报而发现用户有可能或已出现违约、违法、违反公序良俗、侵犯任何一方权益时，则抓饭直播有权采取如下措施：包括但不限于限制用户账户登陆、限制用户在抓饭直播中的活动、删除与账户相关的虚拟资产、删除用户的账户、列入黑名单和要求用户赔偿因用户从事上述行为而给抓饭直播造成的损失（包括但不限于人力成本损失、运营成本损失、商誉损失、维权损失等）、单方面解除本协议、要求用户支付违约金（违约金应当与用户账户内的全部款项相等）、承担赔偿责任，用户在此明确同意承担上述违约责任。</span></div><div id="magicdomid128_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">9.3、赔偿责任的排除及限制</span></div><div id="magicdomid129_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">抓饭直播对于用户使用网络服务或无法使用网络服务所产生的任何直接、间接、衍生的损害或所失利益不负任何损害赔偿责任。若依法无法完全排除损害赔偿责任时，用户认可虚拟资产、虚似物品价值无法确定的虚拟性，并不得以账户或账户内虚拟物品、虚拟资产或者用户使用抓饭直播服务所投入的现金款项等作为赔偿标准，且在任何时候抓饭直播应当承担的责任，均不得超过现存虚拟资产的实际价值，亦仅限于抓饭直播过错引起的直接的金钱利益损失。</span></div><div id="magicdomid130_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid131_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">10. 法律管辖</span></div><div id="magicdomid132_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">10.1、本协议的订立、执行和解释及争议的解决均应适用中华人民共和国法律。</span></div><div id="magicdomid133_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">10.2、本协议的订立、履行、解释及争议的解决均应以抓饭直播所提供之任何形式之电子数据、书面数据等相关内容为准，用户在此确认该等数据的准确性、有效性、合法性。</span></div><div id="magicdomid134_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">10.3、如各方就本协议内容或其执行发生任何争议，应尽量友好协商解决；协商不成时，任何一方均有权提交北京仲裁委员会裁决，按照申请仲裁时该会现行有效的仲裁规则进行仲裁。仲裁的受理费用及处理费用将由提起仲裁一方独立全部承担。</span></div><div id="magicdomid135_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid136_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">11.通知和送达</span></div><div id="magicdomid137_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">11.1 、本协议项下所有的通知均可通过公告、电子邮件或常规的信件传送等方式进行；该等通知于发送之日视为已送达收件人。</span></div><div id="magicdomid138_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid139_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">12. 其他规定</span></div><div id="magicdomid140_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">12.1、本协议构成双方对本协议之约定事项及其他有关事宜的完整协议，除本协议规定的之外，未赋予本协议各方其他权利。</span></div><div id="magicdomid141_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">12.2、如本协议中的任何条款无论因何种原因完全或部分无效或不具有执行力，本协议的其余条款仍应有效并且有约束力。</span></div><div id="magicdomid142_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532">12.3、本协议中的标题仅为方便而设，不具法律或契约效果。</span></div>`