export default `<div class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">尊敬的用户您好，在同意本协议前，请您认真阅读并充分知悉、理解抓饭平台的各项规则及要求，以及国家关于该类互联网信息服务的法律法规等。如果您对本协议的任何条款表示异议，您可以选择不使用；使用则意味着您将同意遵守本协议下全部规定，以及我们后续对本协议随时所作的任何修改，并完全服从于我们的统一管理。本协议已对与您的权益有或可能具有重大关系的条款，及对抓饭平台具有或可能具有免责或限制责任的条款用粗体字予以标注，请您注意。如您需要抓饭平台对相关条款内容予以说明，请您联系我们。</span></div><div id="magicdomid3_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532 font-size:10.5pt">&nbsp;</span></div><div id="magicdomid4_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">第一章 总则</span></div><div id="magicdomid5_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第1条 抓饭平台是抓饭团队(以下统称&ldquo;本团队&rdquo;)向用户提供的一种更方便的进行视频直播、观看视频直播服务并与注册用户进行互动的视频社交平台。抓饭平台将为用户提供虚拟礼物、技术支持、流量入口等进行视频社交所需的服务。</span></div><div id="magicdomid6_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第2条 抓饭平台所有权、经营权、管理权均属本团队。</span></div><div id="magicdomid7_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第3条 本协议最终解释权归属本团队。</span></div><div id="magicdomid8_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid9_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">第二章 抓饭平台账号</span></div><div id="magicdomid10_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第4条 您作为用户注册成功后，抓饭平台将为您提供一个账号（下称&ldquo;抓饭账号&rdquo;）。您在申请使用抓饭平台服务时，应当按照抓饭平台的要求向抓饭平台提供真实、准确的个人资料，以完成实名认证；如个人资料有任何变动，您须及时在抓饭平台上予以更新。您使用抓饭平台产品、服务或者浏览网页，即为同意抓饭平台收集、保有并与合作伙伴分享您的相关个人信息。</span></div><div id="magicdomid11_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第5条 抓饭账号性质上是您接受抓饭平台向您提供服务的凭证；同时是您持有、使用&ldquo;米币&rdquo;以及&rdquo;饭团&rdquo;等相关虚拟财产及进行&ldquo;米币&rdquo;以及&rdquo;饭团&rdquo;兑换的身份凭证。您若需要抓饭平台提供增值服务，抓饭账号同时也是您支付费用、接受增值服务的凭证。</span></div><div id="magicdomid12_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第6条 抓饭账号仅限于您本人使用，禁止赠与、借用、租用、转让或售卖。如果抓饭平台发现使用者并非抓饭账号初始注册人，抓饭平台有权不经通知收回抓饭账号而无需向账号使用人承担法律责任，由此带来的包括并不限于通讯中断、资料和虚拟财产清空等损失由初始注册人自行承担。</span></div><div id="magicdomid13_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第7条 您可自主设定抓饭账号名称、头像、简介等注册信息，不得含有违法和不良信息；如存在该等情况，抓饭平台有权采取通知限期改正、暂停使用、注销登记等措施。</span></div><div id="magicdomid14_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第8条 您同意抓饭平台无偿使用您的名称（包括但不限于真实姓名、笔名、网名及任何代表您身份的文字符号）、肖像（包括但不限于真人肖像及卡通肖像）以及其他相关个人信息进行抓饭平台的各类宣传、向合作伙伴分享、与合作伙伴共同向用户展示用户可能感兴趣的内容等行动。</span></div><div id="magicdomid15_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid16_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">第三章 用户说明</span></div><div id="magicdomid17_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第9条 凡是在抓饭平台下载、浏览和发布内容的用户均为抓饭平台用户。您可在抓饭平台进行视频直播（此时您称为&ldquo;主播&rdquo;）、观看视频直播服务并与其他注册用户互动，赠与或接受其他注册用户或抓饭平台赠与的虚拟礼物，赠与的虚拟礼物一经送出即不可撤销。但您与抓饭平台不构成任何法律层面的雇佣、劳动或劳务或任何与此相似的关系。抓饭平台对主播建立信用等级管理体系，提供与信用等级挂钩的管理和服务。</span></div><div id="magicdomid18_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第10条 您必须年满18周岁。 本协议内容与您所属国家或地区的相关法律法规法令等不相冲突。不具备前述条件的，您应立即终止注册或停止使用。</span></div><div id="magicdomid19_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第11条 您自行承担在抓饭平台进行视频直播和观看视频直播所需要的网络、设备，并保证直播图像和语音质量的清晰、稳定。</span></div><div id="magicdomid20_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第12条 您的个人信息受到保护，除相关国家机关或机构或司法机关依法查询或调查，或您个人自行设置个人信息公开以外，不接受任何个人或单位的查询。</span></div><div id="magicdomid21_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第13条 在不违反适用法律法规的前提下，您享有言论自由的权利。</span></div><div id="magicdomid22_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第14条 您的言行不得违反《计算机信息网络国际联网安全保护管理办法》、《互联网信息服务管理办法》、《互联网电子公告服务管理规定》、《维护互联网安全的决定》、《互联网新闻信息服务管理规定》等相关法律规定，不得在抓饭平台上发布、传播或以其它方式传送含有下列内容之一的直播内容或信息：</span></div><div id="magicdomid23_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 1 ）反对宪法所确定的基本原则的；</span></div><div id="magicdomid24_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 2 ）危害国家安全，泄露国家秘密，颠覆国家政权，破坏国家统一的；</span></div><div id="magicdomid25_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 3 ）损害国家荣誉和利益的；</span></div><div id="magicdomid26_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 4 ）煽动民族仇恨、民族歧视，破坏民族团结的；</span></div><div id="magicdomid27_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 5 ）破坏国家宗教政策，宣扬邪教和封建迷信的；</span></div><div id="magicdomid28_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 6 ）散布谣言，扰乱社会秩序，破坏社会稳定的</span></div><div id="magicdomid29_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 7 ）散布淫秽、色情、赌博、暴力、凶杀、恐怖或者教唆犯罪的；</span></div><div id="magicdomid30_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 8 ）侮辱或者诽谤他人，侵害他人合法权益的；</span></div><div id="magicdomid31_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 9 ）未获他人允许，偷拍、偷录他人，侵害他人合法权利的；</span></div><div id="magicdomid32_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 10 ）煽动非法集会、结社、游行、示威、聚众扰乱社会秩序的；</span></div><div id="magicdomid33_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 11 ）以非法民间组织名义活动的；</span></div><div id="magicdomid34_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 12 ）含有虚假、有害、胁迫、侵害他人隐私、骚扰、侵害、侮辱、诽谤、中伤、粗俗、猥亵、或其它道德上令人反感的内容；</span></div><div id="magicdomid35_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 13 ）侵犯他人知识产权的内容；</span></div><div id="magicdomid36_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">（ 14 ）含有中国法律、法规、规章、条例以及任何具有法律效力之规范所限制或禁止的其它内容的；</span></div><div id="magicdomid37_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第15条 您不得在抓饭平台内直接或间接发布任何形式的广告。</span></div><div id="magicdomid38_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第16条 您不得采用任何形式进行网络赌博活动，包括以&ldquo;米币&rdquo;、&ldquo;虚拟礼物&rdquo;等虚拟财产为标的或赌资进行线上赌博，或者通过直播平台组织线下赌博活动。抓饭将严密监控此类行为，并有权对多个定向账户中异常的、数额较大的虚拟资产流转进行调查，，并有权配合主管机关的调查提交您的身份资料、有关账户的交易记录等信息。</span></div><div id="magicdomid39_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第17条 您不得利用本平台进行任何形式的洗钱活动。抓饭将严密监控此类行为，并有权对多个定向账户中异常的、数额较大的虚拟资产流转进行调查，并有权配合主管机关的调查提交您的身份资料、有关账户的交易记录等信息。</span></div><div id="magicdomid40_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第18条 您通过苹果应用商店为抓饭平台充值的次数每天不能超过20次，如果超过20次则抓饭平台将冻结您的抓饭账户。如果您违规使用非本人苹果手机应用商店代冲值，或者通过其它非抓饭平台认可的渠道非法购买或充值，则抓饭平台有权冻结该抓饭账户，并进行相应惩罚，严重者可以进行封号处理。</span></div><div id="magicdomid41_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第19条 您应承担一切因您个人的行为而直接或间接导致的民事、行政或刑事法律责任，因您违反法律法规或违反本协议的行为给本团队造成损失的，您应负责赔偿。</span></div><div id="magicdomid42_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第20条 禁止任何使用非法软件进行刷内容、违规发布内容的行为。</span></div><div id="magicdomid43_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第21条 您发现抓饭平台上有内容涉嫌违法违规、侮辱或者诽谤他人、侵害他人合法权益、违反本协议或抓饭平台任何运营和管理规则的，有权进行直接举报。</span></div><div id="magicdomid44_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第22条 未经抓饭平台书面同意，您不得在抓饭平台上以任何方式提及第三方竞争平台的相关信息，不得引导用户进入其他第三方竞争平台或为其提供任何信息或便利。</span></div><div id="magicdomid45_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid46_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">第四章 权利声明</span></div><div id="magicdomid47_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第23条 本团队有权制定平台运营规则及注册用户管理规则及相关格式条款，并有权根据运营等情况对相应规则及/或条款进行变更、调整、中止或终止。本团队拥有对抓饭平台上的内容进行监控并对违反本协议的用户进行处理的权利。</span></div><div id="magicdomid48_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第24条 您在抓饭平台发布、直播的内容仅为您个人行为，与本团队无关。对于您的直播内容、言论的真实性、合法性等引发的全部责任，由您自行承担，本团队不承担任何责任。因您发布、直播的内容、言论而引起的法律纠纷，由您自行解决，与本团队无关。如抓饭平台因此遭受任何损失，抓饭平台有权向您追偿，您同意向抓饭平台承担相应的损害赔偿责任，您承担赔偿责任的方式包括但不限于根据抓饭平台的书面通知等向其支付赔偿金、以累计的虚拟资产（如&ldquo;米币&rdquo;）抵偿相关损失等。</span></div><div id="magicdomid49_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第25条 用户之间因使用抓饭平台而产生或可能产生的任何纠纷和/或损失，由用户自行解决并承担相应的责任，与本团队无关。</span></div><div id="magicdomid50_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第26条 抓饭平台未来可能提供部分收费的网络服务，对于此类服务，抓饭平台会在您使用前给与您明确的提示，只有您根据提示确认愿意支付相关费用，您才能使用该等收费网路服务；如果您拒绝支付相关费用，则抓饭平台有权不向您提供该等收费网络服务。</span></div><div id="magicdomid51_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第27条 抓饭平台系统因下列状况无法正常运作，使您无法使用各项服务或任何信息、虚拟财产丢失时，本团队不承担损害赔偿责任，该状况包括但不限于：（1）抓饭平台系统停机维护、升级、调整期间；（2）电信通讯或设备出现故障不能进行数据传输的；（3）因台风、地震、海啸、洪水、停电、战争、恐怖袭击、政府管制等不可抗力之因素，造成抓饭平台系统障碍不能执行业务的；（4）由于黑客攻击、电信部门技术调整或故障、网站升级、相关第三方的问题等原因而造成的服务中断或者延迟。</span></div><div id="magicdomid52_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532 font-size:10.5pt">&nbsp;</span></div><div id="magicdomid53_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">第五章 处罚规则</span></div><div id="magicdomid54_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第28条 凡您发布、直播的内容或信息出现以下情况之一，本团队管理人员有权不提前通知您直接采取停播、删除内容。情节严重者，本团队管理人员有权采取关闭部分或全部权限、暂时封禁抓饭账号或永久封禁抓饭账号、列入黑名单、配合主管部门调查等措施。</span></div><div id="magicdomid55_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">1.发布、传播含有本协议第14条禁止发布、传播的内容的；</span></div><div id="magicdomid56_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">2.发表无意义的灌水内容的；</span></div><div id="magicdomid57_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">3.同一内容多次出现的；</span></div><div id="magicdomid58_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">4.违反本协议第15条的规定，发布广告的；</span></div><div id="magicdomid59_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">5.违反本协议第16条的规定，从事任何形式赌博行为的</span></div><div id="magicdomid60_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">6.违反本协议第17条的规定，从事任何洗钱行为的；</span></div><div id="magicdomid61_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">7.内容包含有严重影响您或其他用户浏览的内容或格式的；</span></div><div id="magicdomid62_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">8.从事非法商业活动的；</span></div><div id="magicdomid63_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">9.假冒管理人员或破坏管理人员形象的；</span></div><div id="magicdomid64_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">10.违反本协议第20条规定，使用非法软件进行刷内容、违规发布内容的行为；</span></div><div id="magicdomid65_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">11.使用不雅或不恰当昵称的；</span></div><div id="magicdomid66_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">12.发布侵犯他人知识产权的内容等且被举报的；</span></div><div id="magicdomid67_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">13.其他本团队认为不恰当的情况。</span></div><div id="magicdomid68_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第29条 如用户在使用抓饭平台时出现本协议第28条之任一情形而导致抓饭账号被暂时或永久封禁的，在此期间您抓饭账户上的剩余&ldquo;米币&rdquo;以及&rdquo;饭团&rdquo;将被暂时冻结或全部扣除，不可继续用于购买抓饭平台上的虚拟产品或服务，同时不予返还您购买&ldquo;米币&rdquo;以及&rdquo;饭团&rdquo;时的现金价值。对于您抓饭账户上存有之剩余&ldquo;米币&rdquo;以及&rdquo;饭团&rdquo;，抓饭平台有权依据系统监控记录信息直接扣除您基于本协议第28条项下的行为而取得的全部虚拟财产所得；如果您的行为给抓饭平台造成损失，抓饭平台有权在抓饭平台的损失范围内扣减您抓饭账户内按照抓饭平台公布的现金兑换规则计算的与损失等额的米币以及&rdquo;饭团&rdquo;；扣除后剩余的米币以及&rdquo;饭团&rdquo;，抓饭平台将与您单独进行结算。</span></div><div id="magicdomid69_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第30条 抓饭平台有权对您在抓饭平台上所发布或直播的内容、发表的言论等提出改进建议和意见，您在收到抓饭平台的建议或意见后3个工作日内进行相应的整改，否则抓饭平台有权采取上述第28条、第29条项下相应的惩罚措施。</span></div><div id="magicdomid70_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第31条 为了能够给广大用户提供一个优质的交流平台，同时使得抓饭平台能够良性、健康的发展，抓饭平台将对涉及反动、色情和发布不良内容的用户、采用任何形式从事线上、线下赌博活动的用户或利用本平台进行洗钱的用户，进行严厉处理。一经发现此类行为，将给予永久封禁、列入黑名单并清空所有内容以及直接扣除抓饭账号上存有的全部虚拟财产（如&ldquo;饭团&rdquo;、&ldquo;米币&rdquo;）的处罚。若抓饭平台认为用户抓饭账号内的或米币来源异常，抓饭平台有权对抓饭账号进行暂时冻结并进行调查。如调查后判定存在来源异常情形则抓饭平台有权将来源异常部分进行没收，情节严重者将进行抓饭账号封禁；来源异常情形包含但不限于：非抓饭平台认可的渠道充值、利用系统BUG、漏洞或外挂获得、其他非正当方式来源等。</span></div><div id="magicdomid71_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid72_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">第六章 知识产权</span></div><div id="magicdomid73_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第32条 抓饭平台的服务包括抓饭平台运营的网站、网页应用、软件及内涵的文字、图片、视频、音频等元素，抓饭平台服务标识、标志及专利权，抓饭平台对前述所有服务、元素、标识、标志等均享有全球范围内独占的知识产权。</span></div><div id="magicdomid74_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第33条 您在抓饭平台上发布的内容，本团队有权转播或引用。</span></div><div id="magicdomid75_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第34条 您在任何时间段在抓饭平台上发表的任何内容的著作权，您许可本团队在全世界范围内免费地、永久性地、不可撤销地使用和可完全转授的权利，包括但不限于：复制权、发行权、出租权、展览权、表演权、放映权、广播权、信息网络传播权、摄制权、改编权、翻译权、汇编权以及《著作权法》规定的由著作权人享有的其他与著作权有关的权利。</span></div><div id="magicdomid76_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第35条 您向抓饭平台作出的第34条所述的授权是独家的，即包括用户之表演和肖像权等合法权利在内，任何第三方未经抓饭平台同意，不得将您在抓饭平台上的任何直播内容、发表言论等进行复制、修改、编辑、转让、使用或作其他用途，包括但不限于截取视频信号、编辑视频/音频/文字和其他形式的内容等行为。您许可本团队有权就任何主体侵权而单独提起诉讼或开展其他法律行动，并获得全部赔偿。</span></div><div id="magicdomid77_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第36条 您不得对抓饭平台服务涉及的相关网页、应用、软件等产品进行反向工程、反向汇编、反向编译等。</span></div><div id="magicdomid78_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第37条 若您在抓饭平台上发布、储存、使用、传播的信息、内容存在侵犯第三人知识产权的情况，抓饭平台提供投诉通道，并将在核实后尽快采取积极行动终止该等行为，但因该等行为而产生的任何法律纠纷和赔偿责任由您自行承担。如抓饭平台因此遭受任何损失，抓饭平台有权向您追偿，您承诺向抓饭平台承担相应的损害赔偿责任，您承担赔偿责任的方式包括但不限于根据抓饭平台的书面通知等向其支付赔偿金、以累计的虚拟资产（如&ldquo;饭团&rdquo;、&ldquo;米币&rdquo;）抵偿相关损失等。</span></div><div id="magicdomid79_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid80_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">第七章 隐私保护</span></div><div id="magicdomid81_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第38条 抓饭平台不对外公开或向第三方提供您的注册资料及您在抓饭平台使用网络服务时存储在抓饭平台的非公开内容，但下列情况除外：</span></div><div id="magicdomid82_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">1.事先获得您的明确授权；</span></div><div id="magicdomid83_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">2.根据有关的法律法规要求；</span></div><div id="magicdomid84_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">3.按照有权政府主管部门或司法机关的要求或为配合主管部门调查；</span></div><div id="magicdomid85_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">4.第三方同意承担与抓饭平台同等的保护您的隐私的责任；</span></div><div id="magicdomid86_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">5.其他抓饭平台认为合适的且法律并不禁止的披露。</span></div><div id="magicdomid87_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">在不透露单个用户隐私资料的前提下，抓饭平台有权对整个用户数据库进行分析并对用户数据库进行商业上的利用。</span></div><div id="magicdomid88_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第39条 您在使用抓饭平台服务时请勿将自认为隐私的信息发表、上传至抓饭平台，也不可将该等信息通过抓饭平台的服务器传播给他人，若因您该等行为引起隐私泄露，由您自行承担责任。</span></div><div id="magicdomid89_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第40条 请您注意勿在使用抓饭平台服务时透露自己的各类财产账户、银行卡、信用卡、第三方支付账户及对应密码等重要资料，否则由此带来的任何损失由您自行承担。</span></div><div id="magicdomid90_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第41条 抓饭平台将采取积极的措施保护您的注册信息，但非抓饭平台可控制的因素导致您信息泄露的，抓饭平台不承担责任。</span></div><div id="magicdomid91_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第42条 抓饭平台软件包含问题反馈功能，在您提交反馈问题和联系方式的同时我们还会收集您的IMEI号、手机型号等相关信息。 您在反馈意见中所填写的联系方式，只会用于我们的客服与您沟通与反馈，除征得您的明确同意和法律明确规定外，抓饭平台不会向任何第三方提供您的上传信息。您的信息不会被用于出售，出借或交换。</span></div><div id="magicdomid92_copy" class="ace-line longKeep gutter-noauthor"><span class="author-p-1481532">&nbsp;</span></div><div id="magicdomid93_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 bold:true">第八章 附则</span></div><div id="magicdomid94_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第43条 抓饭平台保留修改或增补本协议内容的权利。本协议的修改文本将公告于抓饭平台或以其他抓饭平台认为可行的方式公告或发送给您。修改文本一旦公布或发送则立即生效，且对生效前注册的用户同样适用。若您在本协议内容发生修订后，继续使用本服务的，则视为您同意最新修订的协议内容；否则您须立即停止使用抓饭平台提供的相关服务。</span></div><div id="magicdomid95_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第44条 因本协议引起的或与本协议有关的争议，均适用中华人民共和国法律。本协议项下或与之有关的争议，各方应通过友好协商解决，协商不成的，任何一方均有权提交北京仲裁委员会裁决，按照申请仲裁时该会现行有效的仲裁规则进行仲裁。仲裁的受理费用及处理费用将由提起仲裁一方独立全部承担。</span></div><div id="magicdomid96_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第45条 本协议部分内容被有管辖权的法院认定为违法或无效的，不因此影响其他内容的效力。</span></div><div id="magicdomid97_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第46条 本协议未涉及的问题参见国家有关法律法规，当本协议与国家法律法规冲突时，以国家法律法规为准。</span></div><div id="magicdomid98_copy" class="ace-line gutter-author-p-1481532 emptyGutter"><span class="author-p-1481532 font-size:10.5pt">第47条 本协议签订地：深圳。</span></div>`
