import React from 'react'
import realName from './message/realName.js'
import recharge from './message/recharge.js'
import register from './message/register.js'
import withdraw from './message/withdraw.js'

class Protocol extends React.Component{
	constructor(props) {
		super(props)
		this.state = {
			message: {
				realName,
				recharge,
				register,
				withdraw
			}
		}
	}
	render() {
		return (
			<div
				style={{padding: '14px', textAlign: 'justify'}}
				dangerouslySetInnerHTML={{__html: this.state.message[this.props.match.params.id]}}
			></div>
		)
	}
}

export default Protocol